import React, {useRef, useState,useEffect} from 'react'
import {Display} from './Display'
import moment from 'moment'
import Sky from '../img1.jpg'
import './Stylesheets/dates.css'

export const Dates = ({scrolling, getheight}) => {
   var value = moment();
   var key1 =0, key2=-1, key3=0,key4=0;
   var firstDayofYear = value.clone().startOf("year").startOf("month").startOf("week");
   var lastDayofYear = value.clone().endOf("year").endOf("month").endOf("week");
   var yearlastday = value.clone().endOf("year").endOf("month");
   var month = value.clone().startOf("year").startOf("month").subtract(1,"month");
   var day = firstDayofYear.clone().subtract(1, "days");
   var initial = [];
   var randomarr =[5, 9, 12, 25, 32, 60, 78, 83, 90, 95, 103, 118, 126,143,145];
   const [last, setLast] = useState(yearlastday.clone())
   while (day.isBefore(lastDayofYear, "days")){
       initial.push(
           Array(7).fill(0).map(()=> day.add(1,"days").clone())
           )
   };
   const [calendardates, setCalendardates] = useState(initial)
  function newscroll(e){
    var sheight = e.target.scrollHeight -e.target.scrollTop-1;
    var stop =  e.target.clientHeight;
    if(stop>=sheight){
      var first =last.clone().endOf("week").add(1,"days");
      setLast(last.add(1,"year"));
      var day = first.clone().subtract(1,"days");
      var arr=[];
      while(day.isBefore(last.clone().endOf("week"), "days")){
        arr.push(
          Array(7).fill(0).map(()=>day.add(1,"days").clone())
        )
      }
      setCalendardates(calendardates.concat(arr));
    }
  }
  const refscroll = useRef();
  const refDisplay = useRef();
  const refCard = useRef();
  function Expand(e){
    var imageindex =0;
    refDisplay.current.style.display = "block";
   var reference = Number(e.target.getAttribute("data-key"));
   for (let i = 0; i <15; i++) {
     if(reference === randomarr[i]){
      imageindex =i;
       break;
     }
   }
   var width = refCard.current.children[imageindex].clientWidth;
   refCard.current.scrollLeft = imageindex*(width+30)- 0.4*width;
  }
  useEffect(() => {
    getheight(refscroll.current);
    var limit = Math.floor(refscroll.current.children.length/4.35);
    var i =0;
    while(i<limit){
    month.add(1,"months");
    var firstday = month.clone().startOf("week");
    var index = firstday.diff(firstDayofYear, 'days');
    document.getElementById(`${index}`).innerHTML = `${month.format("MMM")} <br/> ${month.format("YYYY")}`
    i++;
    }
  })
 return(
   <>
        <div className = "dates" ref = {refscroll} onScroll= {(e)=>{scrolling(e);newscroll(e)}}>
        {
            calendardates.map((week)=> {
                key1++;
                return (<div className = "weekly" key={key1}>
                {
                  week.map((date)=>{
                    key2++;
                     return(
                        <div>
                      <div key={key2} id={key2}> {date.format("D")} </div>
                      {randomarr.map((integer)=>{
                        key3++;
                        if(key2===integer){
                          return( 
                            <div className="thumbnailcontainer">
                          <div className="material-icons stars">star star star star star_border</div>
                          <section className="thumbnail"><img src={Sky} alt="sky" key={key3} onClick={Expand} data-key={key2}/></section> 
                          <div className= "titlename"> <span>W</span> <span>DC</span> </div>
                          </div>
                          )
                        }
                        return( <div key={key3}></div> )
                      })}
                      </div>
                  )})
                }
            </div>)})
        }
        </div>
        <div className="containerimage" ref= {refDisplay}>
          <div className= "cancel"> <span className="material-icons" onClick={(e)=>e.target.parentElement.parentElement.style.display = "none"}>close</span></div>
          <div className="containsimages" ref={refCard}>
          {
          randomarr.map((a)=>{
            key4++;
            var tracker = value.clone().startOf("year").startOf("month").startOf("week").add((a), "days");
            return( <Display key={key4} tracker = {tracker}/> )
          })}
          </div>
        </div>
        </>
    )
}
